"use client";

import React, { useState } from "react";
import {
    Folder,
    FileText,
    Users,
    Upload,
    Share2,
    ChevronRight,
} from "lucide-react";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";

/* ------------------ Types ------------------ */
interface FileItem {
    id: string;
    name: string;
    type: string;
    size: string;
}

interface FolderItem {
    id: string;
    name: string;
    files: FileItem[];
    sharedWith?: string[];
}

/* ------------------ Static Data ------------------ */
const myFolders: FolderItem[] = [
    {
        id: "1",
        name: "Payslips",
        files: [
            { id: "f1", name: "Payslip_Jan.pdf", type: "PDF", size: "250 KB" },
            { id: "f2", name: "Payslip_Feb.pdf", type: "PDF", size: "260 KB" },
        ],
    },
    {
        id: "2",
        name: "Certificates",
        files: [{ id: "f3", name: "Degree.pdf", type: "PDF", size: "1.2 MB" }],
    },
];

const sharedFolders: FolderItem[] = [
    {
        id: "3",
        name: "HR Documents",
        sharedWith: ["HR Admin"],
        files: [{ id: "f4", name: "Policy.pdf", type: "PDF", size: "800 KB" }],
    },
];

/* ------------------ Main Component ------------------ */
const EmployeeDocumentHub: React.FC = () => {
    const [activeTab, setActiveTab] = useState<"my" | "shared">("my");
    const [selectedFolder, setSelectedFolder] = useState<FolderItem | null>(null);

    const folders = activeTab === "my" ? myFolders : sharedFolders;

    return (
        <div className="p-6 bg-gray-50 min-h-screen">
            <PageBreadcrumb pageTitle="Employee Document Hub" />
            {/* Tabs */}
            <div className="flex gap-4 mb-6">
                <TabButton
                    active={activeTab === "my"}
                    onClick={() => {
                        setActiveTab("my");
                        setSelectedFolder(null);
                    }}
                    label="My Documents"
                />
                <TabButton
                    active={activeTab === "shared"}
                    onClick={() => {
                        setActiveTab("shared");
                        setSelectedFolder(null);
                    }}
                    label="Shared With Me"
                />
            </div>

            <div className="grid grid-cols-12 gap-6">
                {/* Sidebar */}
                <div className="col-span-4 bg-white rounded-xl border p-4">
                    <h2 className="font-medium mb-4 flex items-center gap-2">
                        <Folder size={18} /> Folders
                    </h2>

                    {folders.map((folder) => (
                        <div
                            key={folder.id}
                            onClick={() => setSelectedFolder(folder)}
                            className={`flex items-center justify-between p-3 rounded-lg cursor-pointer mb-2 hover:bg-gray-100 ${selectedFolder?.id === folder.id ? "bg-gray-100" : ""
                                }`}
                        >
                            <div className="flex items-center gap-2">
                                <Folder size={16} />
                                <span>{folder.name}</span>
                            </div>
                            <ChevronRight size={16} />
                        </div>
                    ))}
                </div>

                {/* Content */}
                <div className="col-span-8 bg-white rounded-xl border p-6">
                    {!selectedFolder ? (
                        <div className="text-gray-500 text-center mt-20">
                            Select a folder to view documents
                        </div>
                    ) : (
                        <>
                            <div className="flex justify-between items-center mb-4">
                                <h2 className="text-lg font-semibold flex items-center gap-2">
                                    <Folder size={18} />
                                    {selectedFolder.name}
                                </h2>

                                <div className="flex gap-2">
                                    <ActionButton icon={<Upload size={16} />} label="Upload" />
                                    <ActionButton icon={<Share2 size={16} />} label="Share" />
                                </div>
                            </div>

                            {/* Files */}
                            <div className="space-y-3">
                                {selectedFolder.files.map((file) => (
                                    <div
                                        key={file.id}
                                        className="flex justify-between items-center p-3 border rounded-lg hover:bg-gray-50"
                                    >
                                        <div className="flex items-center gap-3">
                                            <FileText size={18} />
                                            <div>
                                                <p className="font-medium">{file.name}</p>
                                                <p className="text-xs text-gray-500">
                                                    {file.type} â€¢ {file.size}
                                                </p>
                                            </div>
                                        </div>

                                        <button className="text-sm text-brand-600 hover:underline">
                                            Download
                                        </button>
                                    </div>
                                ))}
                            </div>

                            {/* Shared Info */}
                            {selectedFolder.sharedWith && (
                                <div className="mt-6 text-sm text-gray-500 flex items-center gap-2">
                                    <Users size={16} />
                                    Shared with: {selectedFolder.sharedWith.join(", ")}
                                </div>
                            )}
                        </>
                    )}
                </div>
            </div>
        </div>
    );
};

/* ------------------ UI Helpers ------------------ */

const TabButton = ({
    active,
    onClick,
    label,
}: {
    active: boolean;
    onClick: () => void;
    label: string;
}) => (
    <button
        onClick={onClick}
        className={`px-4 py-2 rounded-lg border text-sm font-medium ${active
            ? "bg-brand-500 text-white border-brand-500"
            : "bg-white text-gray-600"
            }`}
    >
        {label}
    </button>
);

const ActionButton = ({
    icon,
    label,
}: {
    icon: React.ReactNode;
    label: string;
}) => (
    <button className="flex items-center gap-2 px-3 py-2 border rounded-lg text-sm hover:bg-gray-50">
        {icon}
        {label}
    </button>
);

export default EmployeeDocumentHub;
